import React from 'react'
import Row from'react-bootstrap/Row';
import Col from'react-bootstrap/Col'
import DashboardIcon from '@mui/icons-material/Dashboard';
import ChatIcon from '@mui/icons-material/Chat';
import CollectionsIcon from '@mui/icons-material/Collections';
import HistoryIcon from '@mui/icons-material/History';
import ScheduleIcon from '@mui/icons-material/Schedule';
import ExploreIcon from '@mui/icons-material/Explore';
import AddAPhotoIcon from '@mui/icons-material/AddAPhoto';
import {Link} from 'react-router-dom';
import {Container} from '@mui/material';
function sideNavbar() {
  return (
    <Container>
   <Row>
    <Col>

<Link to='/'><p><DashboardIcon/>Dashboard</p></Link>
  <Link to='/Messages'><p><ChatIcon/> Messages</p></Link>
   <Link to='/product'><p><CollectionsIcon/>product</p></Link>
   <Link to='/Addphoto'><p><AddAPhotoIcon/> Addphoto</p></Link>
    <Link to='/Graphs'><p><HistoryIcon/>Graphs</p></Link>
     <Link to='/Schedule'><p><ScheduleIcon/> Schedule</p></Link>
<Link to='/explore'><p><ExploreIcon/> Explore</p></Link>
</Col>
   </Row>

    </Container>
  )
}

export default sideNavbar